.App {
  text-align: center;
}

.card {
  display: flex;
  display: "flex";
  align-items: "center";
  justify-content: "center";
}

.border {
  position: relative;
  margin-right: -500px;
  z-index: 1;
}

.card-art {
  border-radius: 30px;
  display: flex;
  position: relative;
}

.Homepage {
  display: flex;
}

.TextOptions {
  padding: 40px
}

.TitleInput {
  width: 300px;
  height: 30px;
  font-size: 20px;
}

.BodyInput {
  width: 300px;
  height: 300px;
  font-size: 20px;
}

.DropDown {
  width: 300px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.Card-Title {
  position: absolute;
  z-index: 2;
  margin-left: 101px;
  height: 50px;
  width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 41px;
  font-family: 'card-title';
}

.Card-Body {
  font-size: 20px;
  position: absolute;
  z-index: 2;
  text-align: justify;
  font-family: 'card-body';
  padding-left: 60px;
  padding-top: 450px;
}

.Card-Type {
  position: absolute;
  z-index: 2;
  text-align: center;
  font-family: 'card-body';
  padding-left: 124px;
  padding-top: 608px;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 65px;
  font-size: 18px;
}

.Download-Button {
  margin-bottom:10px;
}

.clip-art-mask {
  clip-path: polygon(
    0% 5%,
    5% 0%,
    95% 0%,
    100% 5%,
    100% 95%,
    95% 100%,
    5% 100%,
    0% 95%
  );
  width: 500px;
  height: 100%;
}

.Image-Options {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Position-Options {
  display: flex;
  flex-direction: row;
  padding: 40px;
}

@media (prefers-color-scheme: dark) {
  .myclass {
    background: black; 
    color: gray;
  }
}

@media (prefers-color-scheme: light) {
  .myclass {
    background: white;
    color: darkgray;
  }
}

@font-face {
  font-family: 'card-title';
  font-weight: 900;
  src: local('card-title'), url(./assets/fonts/card-title-type.otf) format('opentype');
}

@font-face {
  font-family: 'card-body';
  font-weight: 900;
  src: local('card-body'), url(./assets/fonts/card-text.otf) format('opentype');
}